<template>
    <div id="setting-wrapper">
        <div class="configure-wrapper">
            <div class="label-group">主机</div>
            <el-select class="input-group" v-model="host_id" placeholder="请选择主机" style="width:250px" size="small">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select>
        </div>
        <div class="configure-wrapper">
            <div class="label-group">机房编号</div>
            <el-input class="input-group" v-model="configure.room" style="width:100px" size="small"></el-input>
        </div>
        <div class="configure-wrapper">
            <div class="label-group"></div>
            <el-button type="primary" @click="addConfigure">添加配置</el-button>
        </div>
    </div>
</template>

<script>
import { hostNotGetting, hostSetting } from '../../request/api'
export default {
    data() {
        return {
            configure: {
                room: "",
            },
            host_id:"",
            options: []
        };
    },
    mounted() {
        hostNotGetting().then(res => {
            if (res.data.CODE == 200) {
                let id_list = res.data.DATA
                for (let i = 0; i < id_list.length; i++) {
                    this.options.push({
                        label: id_list[i],
                        value: id_list[i]
                    })
                }
            }
        })
    },
    methods: {
        async addConfigure() {
            if(this.host_id == "" || this.configure.room == ""){
                this.$message.error("参数不能为空")
                return 
            }
            console.log(111)
            let data = {
              HostID:this.host_id,
              Location:this.configure.room
            }
            let res = await hostSetting(data)
            if(res.data.CODE == 200) {
              this.$message.success("上传成功")
            }
        },

    }
};
</script>

<style lang="less" scoped>
@import "../../style/setting";
</style>